<template>
    <swiper :options="swiperOption">
        <swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
            <a :href="slide.url">
                <img width="100%" height="200px" v-lazy="slide.picUrl" alt="">
            </a>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
</template>

<script>
    import 'swiper/dist/css/swiper.css'
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
        name: 'carrousel',
        props: ["swiperSlides"],
        data() {
            return {
                swiperOption: {
                    pagination: {
                        el: '.swiper-pagination'
                    },
                    observer:true,
                　　observeParents:true,
                　　loop:true,
                　　autoplay: {
                　　　　delay: 2000,
                　　    disableOnInteraction: false
                　　},
                    speed: 1000
                }
            }
        },
        components:{
            swiper,
            swiperSlide
        },
    }
</script>
<style lang="less" scoped>

</style>