<template>
    <div class="content">
        <my-scroll class="scroll-content">
            <div class="home">
                <my-slider :swiperSlides="banner">
                </my-slider>
                <div class="recommend-list">
                    <h1 class="text">推荐歌单</h1>
                    <ul class="list">
                        <li class="item" v-for="(item,i) in list" :key="i">
                            <router-link :to="'/playlist?id='+item.id">
                                <img width="115px" height="118px" v-lazy="item.picUrl" alt="">
                            </router-link>
                            <p>{{item.copywriter.slice(0,15)}}</p>
                        </li>
                    </ul>
                </div>
                <div class="recommend-music">
                    <h1 class="text">最新音乐</h1>
                    <ul>
                        <li @click="play(item.id)" class="item" v-for="(item,i) in music" :key="i">
                            <div>
                                <div class="name">
                                    <span>{{item.name}}</span>
                                </div>
                                <div class="author">
                                    <span>{{item.song.artists[0].name}}</span>&nbsp;-
                                    <span>{{item.song.album.name}}</span>
                                </div>
                                <i class="iconfont icon-iconset0481"></i>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </my-scroll>
        <div class="loading" v-show="!banner.length">
            <loading></loading>
        </div>
    </div>
</template>

<script>
    import { getBanner,getRecommendList,getNewMusic } from '@/api/recommend'
    import mySlider from '../base/slider/slider.vue'
    import myScroll from '../base/scroll/scroll.vue'
    import loading from '../base/loading/loading.vue'
    export default {
        name: 'home',
        data () {
            return {
                banner: [],
                list: [],
                music: []
            }
        },
        methods: {
            async getDate() {
                this.banner = await getBanner()
            },
            play(id){
                this.$router.push({path: 'song',query:{id:id}})
            }
        },
        created(){
            this.getDate()
            getRecommendList().then(res => {
                this.list = res
                this.list = this.list.slice(0,12)                
            })
            getNewMusic().then(res => {
                this.music = res 
            })
                        
        },
        components: {
            mySlider,
            myScroll,
            loading
        }
    }
</script>
